

import {
    BRep_Builder, IGESControl_Reader_1,
    OpenCascadeInstance, STEPControl_Reader_1,
    TopoDS_Shape,
} from "opencascade.js/dist/opencascade.full";

const occtio = {
    oc: null as OpenCascadeInstance | null,

    setOpenCascade(openCascade: OpenCascadeInstance) {
        this.oc = openCascade;
    },

    /**
     * https://github.com/donalffons/opencascade.js/issues/222
     * 
     * @param shape
     * @param points
     */
    shapeToBRep(shape : TopoDS_Shape) : string {
        const oc = this.oc!;
        
        var theProgress = new oc.Message_ProgressRange_1();
        var result = oc.BRepTools.Write_3(shape,"test",theProgress)
        let brep = oc.FS.readFile("/" + "test", { encoding:"utf8" } as any);
        oc.FS.unlink("/" + "test");
        // console.log(result)
        // console.log(brep)
        
        return brep as any;
    },

    bRepToShape(shapeBRepText : string) : TopoDS_Shape {
        const oc = this.oc!;
        
        var theProgress = new oc.Message_ProgressRange_1();
        
        oc.FS.writeFile("/" + "test", shapeBRepText, { encoding:"utf8" } as any);

        let shape : TopoDS_Shape = new oc.TopoDS_Shape();
        let bRepBuilder = new oc.BRep_Builder();
        var ok = oc.BRepTools.Read_2(shape,"test", bRepBuilder, theProgress);
        
        oc.FS.unlink("/" + "test");
        
        if (!ok)
        {
            throw new Error("Could not read a shape from string:\n"+shapeBRepText);
        }
        
        return shape;
    },
    
    textToShape(stepText: string, fileType : string) : TopoDS_Shape | any {
        const oc = this.oc!;

        // const fileName = "ubinputs.filename";
        // const stepText = stepText;
        // const fileType = "step";

        console.log(" ### stepText length: ", stepText.length);
        // console.log(" ### stepType: ", fileType);
        // return undefined;
        
        if (fileType === "brep")
        {
            return this.bRepToShape(stepText);
        }
        
        // Writes the uploaded file to Emscripten's Virtual Filesystem
        oc.FS.createDataFile('/', `file.${fileType}`, stepText, true, true, true);
        
        // Choose the correct OpenCascade file parsers to read the CAD file
        let reader: STEPControl_Reader_1 | IGESControl_Reader_1 | any = null;
        
        if (fileType === 'step')
        {
            reader = new oc.STEPControl_Reader_1();
        }
        else if (fileType === 'iges')
        {
            reader = new oc.IGESControl_Reader_1();
        }
        else {
            console.error('opencascade can\'t parse this extension! (yet)');
        }
        
        const readResult = reader.ReadFile(`file.${fileType}`);            // Read the file
        if (readResult === oc.IFSelect_ReturnStatus.IFSelect_RetDone)
        {
            // Translate all transferable roots to OpenCascade
            const messageProgress = new oc.Message_ProgressRange_1();
            const numRootsTransferred = reader.TransferRoots(
                messageProgress
            );
            messageProgress.delete();
            let stepShape = reader.OneShape();
            
            // let adjustedShape;
            
            // if (adjustZtoY)
            // {
            //     let mirroredShape = this.och.mirrorAlongNormal(
            //         { shape: stepShape, origin: [0, 0, 0], normal: [0, 0, 1] }
            //     );
            //     adjustedShape = this.och.rotate({ shape: mirroredShape, axis: [1, 0, 0], angle: 90 });
            //     mirroredShape.delete();
            // }
            
            // Out with the old, in with the new!
            // Remove the file when we're done (otherwise we run into errors on reupload)
            oc.FS.unlink(`/file.${fileType}`);
            
            // if(adjustedShape)
            // {
            //     stepShape.delete();
            //     stepShape = adjustedShape;
            // }
            
            return stepShape;
        } else {
            console.error('Something in OCCT went wrong trying to read a shape from step text: ', stepText);
        }
        return undefined;
    }
        
};

export default occtio;