import { CSGNodeType, CSGOperationType, CSGSolidType, CSGCompoundType, CSGWireType } from './csgTypes'
import occtio from "../occt/occtio";

export default class CSGCompoundFactory
{
    #geometry;
    #nodeCalculator;
    
    constructor(geometry, nodeCalculator) {
        this.#geometry = geometry;
        this.#nodeCalculator = nodeCalculator;
    }

    /**
        return: a shape.
     */
    createNodeShape(node) {
        if (node.Type !== CSGNodeType.Compound) throw new Error(`Given node is not a ${CSGNodeType.Compound}`);

        let shape = undefined;

        switch(node.Compound.Type)
        {
            case CSGCompoundType.FromFile:
                shape = this.#createFromFilE(node.Compound);
                break;

            default:
                throw new Error(`Compound type ${node.Compound.Type} is not supported.`)
        }

        return shape;
    }

    #createFromFilE(compound) {
        let shape = this.#geometry.occtio.textToShape(compound.FileText, compound.FileType);
        return shape;
    }
}